import React from 'react'
import { VStack, HStack, Box, Image, Text, Button, Link } from "@chakra-ui/react";
import About from './About';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
//import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { faLinkedin, faGithub } from "@fortawesome/free-brands-svg-icons"
import { EmailIcon, PhoneIcon } from "@chakra-ui/icons";
import Skills from './Skills';
import Contact from './Contact';
import Projects from './Projects';
import { GitHub } from './GitHub';
import Typewriter from "typewriter-effect"
const Home = () => {
  return (
    <VStack id="home" mt={4}>
      {/* Introduction    */}
      <HStack w="100%" m={5} p={10} >
        <VStack spacing={2} pl={10} w={"50%"} color={"white"}>
          <Text fontSize='4xl' fontWeight="extrabold">Hi👋, My name is</Text>
          <Text id="user-detail-name" fontSize='4xl' fontWeight="extrabold" color={"red.400"}>Deepshikha</Text>
          <Text id="user-detail-intro" fontSize='3xl' fontWeight="extrabold">I am a <span style={{color:"rgb(245,101,101)"}}>
          <Typewriter options={{strings:"Full Stack Web Developer",
          autoStart: true,
          loop: true,
          }}/>
          </span></Text>
          <Text fontSize='l'>Expertise in React.js, Node.js, MongoDB, with knowledge of RESTful
            API design and HTML/CSS. Demonstrated expertise in creating solo
            and collaborative projects. Strong problem-solving abilities and
            excellent communication skills to deliver functional web applications.</Text>
<br/>
          <HStack spacing={8}>
            <Box bgColor="red.400" boxSize={10} pt={1}>
              <a href="https://www.linkedin.com/in/deep-shikha-338a051a6/"><FontAwesomeIcon icon={faLinkedin} size="2x" /> </a>
            </Box>
            <Box bgColor="red.400" boxSize={10} pt={1}>
              <a href="https://github.com/Deep-06"><FontAwesomeIcon icon={faGithub} size="2x" /></a>
            </Box>

            <Link href="mailto:shikhadeep.hzp@gmail.com" bgColor="red.400" p={2}
              borderRadius={10} >
              <EmailIcon boxSize={7} />
            </Link>

            <Link href="tel:+91 9572221994" bgColor="red.400" p={2}
              borderRadius={10}>
              <PhoneIcon boxSize={7} />
            </Link>
            <Button bgColor="red.400" p={3} pt={1} >
              <a id="resume-button-2" href="https://drive.google.com/file/d/1eYvu_wga1fN32_WcDARJB2etLEWhg0JZ/view?usp=sharing">Resume</a>
            </Button>
          </HStack>
        </VStack>

        <Box w={"50%"} p={5} ml={40} >
          <Image id="home-img" className="home-img" src="/Images/Profilepic2.jpeg" alt="Image" width="70%" borderRadius="50%" />
        </Box>
      </HStack>

      {/* About Us */}
      <Box id="about" className="about.about.section">
        <About />
      </Box>


      {/* Skills */}
      <Box id="skills" className="skill-section">
        <Skills />
      </Box>

      {/* Projects */}
      <Box id="projects" className="project-section">
        <Projects />
      </Box>

      {/* Github Stats */}
      <Box id="github" className="github-section">
        <GitHub />
      </Box>

      {/* Contact */}
      <Box id="contact" className="contact-section">
        <Contact />
      </Box>
    </VStack>
  )
}

export default Home
